<template>
<div>
  <cinema-logo></cinema-logo>
  <navi-panel></navi-panel>
  <div>
    <h2>armchair</h2>
    <a @click="getTestAll(1)">getTestALL</a>
    <a @click="getTest()">getTest</a>
    <input v-model="armchair.name" placeholder="name">
    <input v-model="armchair.surname" placeholder="surname">
    <input v-model="armchair.dateOfBirth" placeholder="data">
    <a @click="postTest()">postTest</a>
    <input v-model="armchairId" placeholder="ID">
    <a @click="deleteTest()">deleteTest</a>
    <tr v-for="ar in newArmchair">
      <td>{{ar.id}}</td>
      <!-- <td>{{ar.room}}</td> -->
      <td>{{ar.row}}</td>
      <td>{{ar.seat_number}}</td>
      <td>{{ar.armchair_type}}</td>
      <td>{{ar.coordinates_X}}</td>
      <td>{{ar.coordinates_Y}}</td>
    </tr>
    <div>Room</div>
    <tr v-for="ro in newRoom">
      <td>{{ro}}</td>
    </tr>
  </div>
</div>

</template>

<script>
  export default {
    data() {
      return {
        armchairId: 0,
        newArmchair: {},
        newRoom: {},
        armchair: {
        },
        values: [] = ["https://www.msennik.pl/wp-content/uploads/2013/10/sennik_jeden.jpg",
          "https://www.w3schools.com/w3css/img_lights.jpg",
          "https://www.gettyimages.ie/gi-resources/images/Homepage/Hero/UK/CMS_Creative_164657191_Kingfisher.jpg"],
        slide: 0,
        sliding: null
      }
    },
    methods: {
      onSlideStart(slide) {
        this.sliding = true
      },
      onSlideEnd(slide) {
        this.sliding = false
      },
      // get test dziala
      getTest(){
        this.$http.get(`http://localhost:8080/api/getRoom`)
        .then(response => {
          this.newRoom = response.data.data
          // this.actor.id_movie = this.newMovie[1].id
          // console.log(response.data)
        })

        this.$http.get(`http://localhost:8080/api/getArmchair`)
        .then(response => {
          this.newArmchair = response.data.data
          console.log(response.data)
        })
      },
      getTestAll(id){
        this.$http.get(`http://localhost:8080/api/getArmchairByRoomId`, {params: {roomId: id}})
        .then(response => {
          // this.newArmchair = response.data.data
          console.log(response.data)
        })
      },
      // post test dziala
      postTest(){
        console.log("asd")
        this.$http.post('http://localhost:8080/api/addOrUpdateArmchair', this.armchair).then(function (response) {
          // Success
          console.log(response.data)
          },function (response) {
            // Error
            console.log(response.data)
            });
      },
      // delete test dziala
      deleteTest(){
        this.$http.delete('http://localhost:8080/api/deleteArmchair', {body: this.armchairId}).then(function (response) {
          // Success
          console.log(response.data)
          },function (response) {
            // Error
            console.log(response.data)
            });
      }
    }
  }
</script>

<style>
  b-carousel-slide img-src {
    height: 100px;
    width: 100px;
  }
  /*.img-fluid {*/
    /*height: min-content;*/
  /*}*/

  .account-submenu {
    float: right !important;
  }

  .movie_image {
    height: 20%;
    width: 20%;
  }

  .center_header {
    background-color: aqua;
    text-align: center;
  }

  .opis {
    margin-right: 15px;
    margin-left: 15px;
    margin-bottom: 40px;
  }

  .movie_image {
    display: inline-block;
  }

  .movie_desc {
    display: inline-block;
  }
</style>



