<template>
<div>
  <cinema-logo></cinema-logo>
  <navi-panel></navi-panel>
  <div>
    <h2>room</h2>
    <a @click="getByCinemaId(1)">GETBYID</a>
    <a @click="getTest()">getTest</a>
    <input v-model="room.name" placeholder="name">
    <input v-model="room.surname" placeholder="surname">
    <input v-model="room.dateOfBirth" placeholder="data">
    <a @click="postTest()">postTest</a>
    <input v-model="roomId" placeholder="ID">
    <a @click="deleteTest()">deleteTest</a>
    <tr v-for="ro in newRoom">
      <td>{{ro.id}}</td>
      <td>{{ro.room_number}}</td>
    </tr>
    <div>Cinema</div>
    <tr v-for="ci in newCinema">
      <td>{{ci}}</td>
    </tr>
  </div>
</div>

</template>

<script>
  export default {
    data() {
      return {
        cinemaid: 5, //to bedzie z localstorage czy cos
        roomId: 0,
        newRoom: {},
        newCinema: {},
        room: {
        },
        values: [] = ["https://www.msennik.pl/wp-content/uploads/2013/10/sennik_jeden.jpg",
          "https://www.w3schools.com/w3css/img_lights.jpg",
          "https://www.gettyimages.ie/gi-resources/images/Homepage/Hero/UK/CMS_Creative_164657191_Kingfisher.jpg"],
        slide: 0,
        sliding: null
      }
    },
    methods: {
      onSlideStart(slide) {
        this.sliding = true
      },
      onSlideEnd(slide) {
        this.sliding = false
      },
      // get test dziala
      getTest(){
        this.$http.get(`http://localhost:8080/api/getCinema`)
        .then(response => {
          this.newCinema = response.data.data
          console.log(response.data)
        })
        
        this.$http.get(`http://localhost:8080/api/getRoom`)
        .then(response => {
          this.newRoom = response.data.data
          console.log(response.data)
        })
      },
      getByCinemaId(id){
        this.$http.get(`http://localhost:8080/api/getRoomByCinemaId`, {params: {cinemaId: id}})
        .then(response => {
          this.newRoom = response.data.data
          console.log(response.data)
        })
      },
      // post test dziala
      postTest(){
        console.log("asd")
        this.$http.post('http://localhost:8080/api/addOrUpdateRoom', this.room).then(function (response) {
          // Success
          console.log(response.data)
          },function (response) {
            // Error
            console.log(response.data)
            });
      },
      // delete test dziala
      deleteTest(){
        this.$http.delete('http://localhost:8080/api/deleteRoom', {body: this.roomId}).then(function (response) {
          // Success
          console.log(response.data)
          },function (response) {
            // Error
            console.log(response.data)
            });
      }
    }
  }
</script>

<style>
  b-carousel-slide img-src {
    height: 100px;
    width: 100px;
  }
  /*.img-fluid {*/
    /*height: min-content;*/
  /*}*/

  .account-submenu {
    float: right !important;
  }

  .movie_image {
    height: 20%;
    width: 20%;
  }

  .center_header {
    background-color: aqua;
    text-align: center;
  }

  .opis {
    margin-right: 15px;
    margin-left: 15px;
    margin-bottom: 40px;
  }

  .movie_image {
    display: inline-block;
  }

  .movie_desc {
    display: inline-block;
  }
</style>



