// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import BootstrapVue from 'bootstrap-vue'
import VueResources from "vue-resource"
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import './assets/style.css';

Vue.config.productionTip = true;
Vue.use(BootstrapVue);
Vue.use(VueResources);

Vue.component('cinema-logo', {
  template: `<div class="cinema-logo-image">
      <b-navbar toggleable="md" type="dark" variant="info">
      <h6>nazwa kina</h6>
        <a href="#" class = "b-navbar-brand">
        <img src="https://www.10forward.ae/wp-content/uploads/2015/05/video_icon.png" width="30" height="30"></a>
    <b-navbar-nav class="ml-auto">
      <b-nav-item-dropdown right>
        <!-- Using button-content slot -->
        <template slot="button-content">
          <em>User</em>
        </template>
        <b-dropdown-item href="#">Profile</b-dropdown-item>
        <b-dropdown-item href="#">Signout</b-dropdown-item>
      </b-nav-item-dropdown>
    </b-navbar-nav>
    </b-navbar>
    </div>`
});

Vue.component('navi-panel', {
  template: `<b-navbar toggleable="md" type="dark" variant="info">

  <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

  <b-collapse is-nav id="nav_collapse">
    <b-navbar-nav>
      <b-nav-item href="#">Repertuar</b-nav-item>
      <b-nav-item href="#">Filmy</b-nav-item>
      <b-nav-item href="#">Zapowiedzi</b-nav-item>
      <b-nav-item href="#">Cennik</b-nav-item>
      <b-nav-item href="#">Oferta i promocje</b-nav-item>
      <b-nav-item href="#">O nas</b-nav-item>
      <b-nav-item href="#">Kontakt</b-nav-item>
    </b-navbar-nav>
  </b-collapse>
</b-navbar>`
});

new Vue({
  el: '#app',
  router,
  components: {App},
  template: '<App/>'
})



