<template>
  <div>
    <cinema-logo></cinema-logo>
    <navi-panel></navi-panel>
    <div class="main-carousel">
      <b-carousel id="carousel1"
                  style="text-shadow: 1px 1px 2px #333;"
                  controls
                  indicators
                  background="#ababab"
                  :interval="4000"
                  v-model="slide"
                  @sliding-start="onSlideStart"
                  @sliding-end="onSlideEnd">

        <b-carousel-slide v-for="it in values">
          <img slot="img" class="d-block w-100" height="500"
               :src="it.urlImageLink" alt="image slot">
        </b-carousel-slide>
      </b-carousel>
    </div>
    <div class="row">
      <div class="col-sm-6 small-carousel">
        <b-carousel id="carousel1"
                    style="text-shadow: 1px 1px 2px #333;"
                    controls
                    indicators
                    background="#ababab"
                    :interval="4000"
                    img-width="200"
                    img-height="300"
                    v-model="slide"
                    @sliding-start="onSlideStart"
                    @sliding-end="onSlideEnd">

          <b-carousel-slide v-for="it in values">
            <img slot="img" class="d-block w-100" height="300"
                 :src="it" alt="image slot">
          </b-carousel-slide>

        </b-carousel>
      </div>
      <div class="col-sm-6 small-carousel">
        <b-carousel id="carousel1"
                    style="text-shadow: 1px 1px 2px #333;"
                    controls
                    indicators
                    background="#ababab"
                    :interval="4000"
                    img-width="200"
                    img-height="300"
                    v-model="slide"
                    @sliding-start="onSlideStart"
                    @sliding-end="onSlideEnd">

          <b-carousel-slide v-for="it in values">
            <img slot="img" class="d-block w-100" height="300"
                 :src="it.urlImageLink" alt="image slot">
          </b-carousel-slide>

        </b-carousel>
      </div>
    </div>
    <div style="margin-top: 30px; margin-bottom: 30px">
      <h2 align="center">REPERTUAR</h2>
      <tr v-for="it in items">
        <td @click="consoleLogValue(it.message)">{{it.message}}</td>
        <td>{{it.value}}</td>
      </tr>
    </div>
  </div>

</template>

<script>
  export default {
    data() {
      return {
        values: {},
        // values: [] = ["https://www.msennik.pl/wp-content/uploads/2013/10/sennik_jeden.jpg",
        //   "https://www.w3schools.com/w3css/img_lights.jpg",
        //   "https://www.gettyimages.ie/gi-resources/images/Homepage/Hero/UK/CMS_Creative_164657191_Kingfisher.jpg"],
        items: [
          {
            message: 'message1',
            value: "asd1"
          },
          {
            message: 'message2',
            value: "asd2"
          },
          {
            message: 'message3',
            value: "asd3"
          },
          {
            message: 'message4',
            value: "asd4"
          },
          {
            message: 'message5',
            value: "asd5"
          },
        ],
        slide: 0,
        sliding: null
      }
    },

    methods: {
      onSlideStart(slide) {
        this.sliding = true
      },
      onSlideEnd(slide) {
        this.sliding = false
      },
      consoleLogValue(value){
        console.log(value);
      }
    },
    created: function(){
      this.$http.get(`http://localhost:8080/api/getMovie`)
        .then(response => {
          this.values = response.data.data
        })
         
    }
  }
</script>

<style>
  .row{
    margin-right: unset;
    margin-left: unset;
  }
  .main-carousel {
    margin-bottom: 30px;
    margin-top: 30px;
  }

  .small-carousel {
    margin-bottom: 30px;
    margin-top: 30px;
  }


</style>



