<template>
<div>
  <cinema-logo></cinema-logo>
  <navi-panel></navi-panel>
  <div>
    <h2>STAFF</h2>
    <a @click="getTest()">getTest</a>
    <input v-model="staff.id" placeholder="id">
    <input v-model="staff.name" placeholder="name">
    <input v-model="staff.surname" placeholder="surname">
    <input v-model="staff.dateOfBirth" placeholder="data">
    <a @click="postTest()">postTest</a>
    <input v-model="staffId" placeholder="ID">
    <a @click="deleteTest()">deleteTest</a>
    <tr v-for="st in newStaff">
      <td @click="setStaff(st)">{{st.id}}</td>
      <td>{{st.name}}</td>
      <td>{{st.surname}}</td>
      <td>{{st.dateOfBirth}}</td>
    </tr>
  </div>
</div>

</template>

<script>
  export default {
    data() {
      return {
        staffId: 0,
        newStaff: {},
        staff: {
        },
        values: [] = ["https://www.msennik.pl/wp-content/uploads/2013/10/sennik_jeden.jpg",
          "https://www.w3schools.com/w3css/img_lights.jpg",
          "https://www.gettyimages.ie/gi-resources/images/Homepage/Hero/UK/CMS_Creative_164657191_Kingfisher.jpg"],
        slide: 0,
        sliding: null
      }
    },
    methods: {
      onSlideStart(slide) {
        this.sliding = true
      },
      onSlideEnd(slide) {
        this.sliding = false
      },
      setStaff(st){
        this.staff.id = st.id;
        this.staff.name = st.name;
        this.staff.surname = st.surname;
        this.staff.dateOfBirth = st.dateOfBirth
        console.log(this.staff);
      },
      // get test dziala
      getTest(){
        this.$http.get(`http://localhost:8080/api/getStaff`)
        .then(response => {
          this.newStaff = response.data.data
          console.log(response.data)
        })
      },
      // post test dziala
      postTest(){
        console.log("asd")
        this.$http.post('http://localhost:8080/api/addOrUpdateStaff', this.staff).then(function (response) {
          // Success
          console.log(response.data)
          },function (response) {
            // Error
            console.log(response.data)
            });
      },
      // delete test dziala
      deleteTest(){
        this.$http.delete('http://localhost:8080/api/deleteStaff', {body: this.staffId}).then(function (response) {
          // Success
          console.log(response.data)
          },function (response) {
            // Error
            console.log(response.data)
            });
      }
    }
  }
</script>

<style>
  b-carousel-slide img-src {
    height: 100px;
    width: 100px;
  }
  /*.img-fluid {*/
    /*height: min-content;*/
  /*}*/

  .account-submenu {
    float: right !important;
  }

  .movie_image {
    height: 20%;
    width: 20%;
  }

  .center_header {
    background-color: aqua;
    text-align: center;
  }

  .opis {
    margin-right: 15px;
    margin-left: 15px;
    margin-bottom: 40px;
  }

  .movie_image {
    display: inline-block;
  }

  .movie_desc {
    display: inline-block;
  }
</style>



