<template>
<div>
  <cinema-logo></cinema-logo>
  <navi-panel></navi-panel>
  <div>
    <h2>actor</h2>
    <a @click="getTest()">getTest</a>
    <input v-model="actor.id.id_staff.id" placeholder="id">
    <input v-model="actor.id.id_movie.id" placeholder="surname">
    <a @click="postTest()">postTest</a>
    <input v-model="actorId" placeholder="ID">
    <a @click="deleteTest()">deleteTest</a>
    <div>ACTOR</div>
    <tr v-for="ac in newActor">
      <td>{{ac}}</td>
    </tr>
    <div>Staff</div>
    <tr v-for="st in newStaff">
      <td @click="setStaff(st)">{{st}}</td>
    </tr>
    <div>Movie</div>
    <tr v-for="mv in newMovie">
      <td @click="setMovie(mv)">{{mv}}</td>
    </tr>
  </div>
</div>

</template>

<script>
  export default {
    data() {
      return {
        newMovie: {},
        newStaff: {},
        actorId: 0,
        newActor: {
        },
        actor: {
          id: {
            id_staff: {},
            id_movie: {},
          }
        },
        values: [] = ["https://www.msennik.pl/wp-content/uploads/2013/10/sennik_jeden.jpg",
          "https://www.w3schools.com/w3css/img_lights.jpg",
          "https://www.gettyimages.ie/gi-resources/images/Homepage/Hero/UK/CMS_Creative_164657191_Kingfisher.jpg"],
        slide: 0,
        sliding: null
      }
    },
    methods: {
      onSlideStart(slide) {
        this.sliding = true
      },
      onSlideEnd(slide) {
        this.sliding = false
      },
      setStaff(staff){
        this.actor.id.id_staff = staff
      },
      setMovie(movie){
        this.actor.id.id_movie = movie
        console.log(this.newActor[0])
        console.log(this.actor)
      },
      // get test dziala
      getTest(){

        this.$http.get(`http://localhost:8080/api/getMovie`)
        .then(response => {
          this.newMovie = response.data.data
          // this.actor.id_movie = this.newMovie[1].id
          // console.log(response.data)
        })
        
        this.$http.get(`http://localhost:8080/api/getStaff`)
        .then(response => {
          this.newStaff = response.data.data
          // this.actor.id_staff = this.newStaff[2].id
          // console.log(response.data)
        })

      
        this.$http.get(`http://localhost:8080/api/getActor`)
        .then(response => {
          this.newActor = response.data.data
          // this.newActor[0].id.id_staff = this.newStaff[2]
          // console.log(response.data)
          // console.log(this.newActor[0])
        })
        
        // this.newActor.id_staff = this.newStaff[2];
        // console.log(this.actor)
        // console.log(this.newMovie, this.newActor)

        // this.newActor.id_movie = this.newMovie[0];
        // this.newActor.id_staff = this.newStaff[0];   
        
        // console.log(this.actor)
      },
      // post test problem z przesylaniem danych
      postTest(){
        // console.log(this.newActor[0])
        this.$http.post('http://localhost:8080/api/addOrUpdateActor', this.actor)
        .then((response) => {
          // Success
          console.log(response.data)
          },(response) => {
            // Error
            console.log(response.data)
            });
      },
      // delete test dziala
      deleteTest(){
        this.$http.delete('http://localhost:8080/api/deleteActor', {body: this.actorId}).then(function (response) {
          // Success
          console.log(response.data)
          },function (response) {
            // Error
            console.log(response.data)
            });
      }
    }
  }
</script>

<style>
  b-carousel-slide img-src {
    height: 100px;
    width: 100px;
  }
  /*.img-fluid {*/
    /*height: min-content;*/
  /*}*/

  .account-submenu {
    float: right !important;
  }

  .movie_image {
    height: 20%;
    width: 20%;
  }

  .center_header {
    background-color: aqua;
    text-align: center;
  }

  .opis {
    margin-right: 15px;
    margin-left: 15px;
    margin-bottom: 40px;
  }

  .movie_image {
    display: inline-block;
  }

  .movie_desc {
    display: inline-block;
  }
</style>



